.app-container {
  display: flex;
  height: 100vh;
  background-color: #1A1B1F;
  color: #fff;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.content-container {
  flex: 1;
  position: relative;
  overflow: hidden;
}

.page {
  height: 100%;
  width: 100%;
}

.loading-screen {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}

.loading-logo {
  width: 100px;
  height: 100px;
}